export default function uiAnimations() {
  const info = document.querySelector('.info');
  const nav = document.querySelector('.home__foot > fieldset');
  const infoAnchor = document.querySelectorAll('#info_anchor > span');
  const statsContainer = document.querySelector('.home__stats-container');

  let infoShowing = false;

  function handleInfoChange() {
    if (!infoShowing) {
      infoShowing = true;
      // Modal animation
      info.classList.add('info-enter--active');
      // Version select animation
      nav.classList.add('fieldset--exit');
      // Info button animation
      infoAnchor[0].classList.add('fieldset--exit');
      infoAnchor[1].classList.add('fieldset--enter--reversed');

      if (statsContainer) {
        statsContainer.classList.add('home__stats-container--hide');
      }
    } else {
      infoShowing = false;
      // Modal animation
      info.classList.remove('info-enter--active');
      info.classList.add('info-exit--active');
      info.addEventListener('animationend', handleInfoOut);
      // Version select animation
      nav.classList.remove('fieldset--exit');
      nav.classList.add('fieldset--enter');
      // Info button animation
      infoAnchor[0].classList.remove('fieldset--exit');
      infoAnchor[1].classList.remove('fieldset--enter--reversed');
      infoAnchor[0].classList.add('fieldset--enter');
      infoAnchor[1].classList.add('fieldset--exit--reversed');

      if (statsContainer) {
        statsContainer.classList.remove('home__stats-container--hide');
      }
    }
  }

  function handleInfoOut() {
    nav.classList.remove('fieldset--enter');
    info.classList.remove('info-exit--active');
    infoAnchor[0].classList.remove('fieldset--enter');
    infoAnchor[1].classList.remove('fieldset--exit--reversed');
  }

  return {
    handleInfoChange,
  };
}
