// import * as dat from 'dat.gui';
import Stats from 'stats-js';
import CircleWaveAnimation from './animation';
import initGui, { DEFAULTS } from './helpers/gui';
import uiAnimations from './helpers/uiAnimations';

function JE_PLAY_CIRCLEWAVE() {
  const CONSTANTS = {
    height: document.body.clientHeight,
    width: document.body.clientWidth,
    mode: '1',
  };

  const ANIMATIONS = uiAnimations();

  const modeSelector = document.getElementsByName('waveVersions');
  modeSelector.forEach(el => {
    el.addEventListener('change', handleModeChange);
    if (el.checked) {
      CONSTANTS.mode = el.value;
    }
  });
  document
    .querySelector('#info')
    .addEventListener('change', ANIMATIONS.handleInfoChange);
  const canvas = document.querySelector('.home__canvas');
  const ctx = canvas.getContext ? canvas.getContext('2d') : null;

  const init = () => {
    canvas.height = CONSTANTS.height;
    canvas.width = CONSTANTS.width;

    /**
     * create new animation class
     */
    this.animation = CircleWaveAnimation(ctx, DEFAULTS);

    /**
     * set up stats
     */
    const statsContainer = document.querySelector('.home__stats-container');
    if (statsContainer) {
      this.stats = new Stats();
      this.stats.showPanel(0);
      statsContainer.append(this.stats.dom);
    }

    initGui(DEFAULTS);
    window.addEventListener('resize', handleResize);

    /**
     * begin playing
     */
    if (!this.playing) {
      this.playing = true;
      update();
    }
  };

  const update = () => {
    if (this.stats) {
      this.stats.begin();
    }

    this.animation.update();

    if (this.playing) {
      draw();
      if (this.stats) {
        this.stats.end();
      }
      requestAnimationFrame(update);
    }
  };

  const draw = () => {
    const colors = [DEFAULTS.color01, DEFAULTS.color02, DEFAULTS.color03];
    this.animation.draw(
      colors,
      CONSTANTS.mode,
      CONSTANTS.width,
      CONSTANTS.height,
    );
  };

  const handleResize = () => {
    CONSTANTS.width = document.body.clientWidth;
    CONSTANTS.height = document.body.clientHeight;

    canvas.width = document.body.clientWidth;
    canvas.height = document.body.clientHeight;
  };

  function handleModeChange({ target: { value } }) {
    CONSTANTS.mode = value;
  }

  /**
   * Start this baby
   */
  init();
}

document.addEventListener('DOMContentLoaded', JE_PLAY_CIRCLEWAVE);
