/* eslint-disable no-restricted-properties */
import { lineRadial, curveLinearClosed } from 'd3-shape';

const N = 360;

function Line(ctx, color, index, defaults) {
  const pathArray = Array.from({ length: N }, (_, i) => {
    const a = i * 2 * (Math.PI / N);
    const t = performance.now() * defaults.speed;
    const c = Math.cos(a * defaults.frequency - index * defaults.shift + t);
    const p = Math.pow((1 + Math.cos(a - t)) / 2, 3);

    return defaults.radius + defaults.amplitude * c * p;
  });

  ctx.beginPath();

  /**
   *
   * lets replace this so we don't have to use D3-shape
   *
   */

  lineRadial()
    .context(ctx)
    .curve(curveLinearClosed)
    .angle(...pathArray)
    .radius(r => r)(pathArray);

  ctx.lineWidth = defaults.width;
  ctx.strokeStyle = color;
  ctx.stroke();
}

export default Line;
