import * as dat from 'dat.gui';

export const DEFAULTS = {
  color01: '#5DA9E2',
  color02: '#FF6F85',
  color03: '#7eff8e',

  width: 8,
  speed: 0.001,
  frequency: 8,
  amplitude: 32,
  radius: 256,
  shift: (2 * Math.PI) / 3,
  pixelAmount: 360,
  composition: 'lighten',
};

function initGui(defaults) {
  const GUI = new dat.GUI({ autoPlace: false });
  GUI.add(defaults, 'composition', ['lighten', 'darken']);
  GUI.add(defaults, 'width', 0, 100);
  GUI.add(
    defaults,
    'radius',
    0,
    Math.min(document.body.clientHeight, document.body.clientWidth) / 2,
  );
  GUI.add(defaults, 'speed', 0, 0.1).step('0.001');
  GUI.add(defaults, 'shift', 0, 2 * Math.PI).step('0.001');
  GUI.add(defaults, 'frequency').step('1');
  GUI.add(defaults, 'amplitude');
  GUI.add(defaults, 'pixelAmount', 1, 1800);

  const colors = GUI.addFolder('Colours');
  colors.addColor(defaults, 'color01');
  colors.addColor(defaults, 'color02');
  colors.addColor(defaults, 'color03');
  colors.open();

  const guiContainer = document.querySelector('.home__gui');
  guiContainer.appendChild(GUI.domElement);
}

export default initGui;
