import Circle from './components/circle';
import Dot from './components/dots';
import Line from './components/line';

function CircleWaveAnimation(ctx, defaults) {
  const update = () => {};

  const draw = (colors, mode, width, height) => {
    ctx.clearRect(0, 0, width, height);
    ctx.save();
    ctx.translate(width / 2, height / 2);
    ctx.lineWidth = defaults.lineWidth;
    ctx.lineJoin = 'round';
    ctx.globalCompositeOperation = defaults.composition;

    colors.forEach((color, index) => {
      switch (mode) {
        case '1':
          Circle(ctx, color, index, defaults);
          break;
        case '2':
          Dot(ctx, color, index, defaults);
          break;
        case '3':
          Line(ctx, color, index, defaults);
          break;
        default:
          break;
      }
    });

    ctx.restore();
  };

  return {
    update,
    draw,
  };
}

export default CircleWaveAnimation;
