/* eslint-disable no-restricted-properties */
const N = 360;

function Circle(ctx, color, index, defaults) {
  const pathArray = Array.from({ length: N }, (_, i) => {
    const a = i * 2 * (Math.PI / N);
    const t = performance.now() * defaults.speed;
    const c = Math.cos(a * defaults.frequency - index * defaults.shift + t);
    const p = Math.pow((1 + Math.cos(a - t)) / 2, 3);
    return defaults.radius + defaults.amplitude * c * p;
  }).map((r, i) => ({
    x: r * Math.cos((Math.PI / (N / 2)) * i),
    y: r * Math.sin((Math.PI / (N / 2)) * i),
  }));

  ctx.beginPath();

  let i = 0;

  for (i = 0; i < pathArray.length - 1; i += 1) {
    const xc = (pathArray[i].x + pathArray[i + 1].x) / 2;
    const yc = (pathArray[i].y + pathArray[i + 1].y) / 2;
    ctx.quadraticCurveTo(pathArray[i].x, pathArray[i].y, xc, yc);
  }

  ctx.lineWidth = defaults.width;
  ctx.strokeStyle = color;
  ctx.closePath();
  ctx.stroke();
}

export default Circle;
